"use client";

import { useState } from "react";

export default function VideoTest() {
  const [videoStatus, setVideoStatus] = useState<Record<number, string>>({});

  const testVideos = [
    { id: 1, src: "/videos/test.mp4", label: "Local video" },
    {
      id: 2,
      src: "https://www.w3schools.com/html/mov_bbb.mp4",
      label: "W3Schools video",
    },
    {
      id: 3,
      src: "https://commondatastorage.googleapis.com/gtv-videos-bucket/sample/BigBuckBunny.mp4",
      label: "Google CDN video",
    },
    {
      id: 4,
      src: "https://storage.googleapis.com/web-dev-assets/video-and-source-tags/chrome.mp4",
      label: "Another test video",
    },
  ];

  const updateStatus = (id: number, status: string) => {
    setVideoStatus((prev) => ({ ...prev, [id]: status }));
  };

  return (
    <div className="p-4 max-w-md mx-auto">
      <h1 className="text-2xl font-bold mb-4">Video Test Page</h1>
      <p className="mb-4 text-sm">
        Testing video playback on different sources and formats.
      </p>

      {testVideos.map((video) => (
        <div key={video.id} className="mb-8 border rounded-lg p-4">
          <h2 className="font-semibold mb-2">{video.label}</h2>
          <p className="text-xs mb-2">{video.src}</p>

          <div className="relative bg-gray-900 rounded overflow-hidden">
            <video
              id={`video-${video.id}`}
              controls
              width="100%"
              height="auto"
              preload="metadata"
              playsInline
              className="w-full"
              onLoadStart={() => updateStatus(video.id, "Loading started")}
              onLoadedMetadata={() => updateStatus(video.id, "Metadata loaded")}
              onLoadedData={() => updateStatus(video.id, "Data loaded")}
              onCanPlay={() => updateStatus(video.id, "Can play")}
              onCanPlayThrough={() =>
                updateStatus(video.id, "Can play through")
              }
              onPlay={() => updateStatus(video.id, "Playing")}
              onError={(e) => {
                console.error(`Video ${video.id} error:`, e);
                updateStatus(video.id, "Error loading");
              }}
            >
              <source
                src={video.src}
                type={video.src.includes(".mp4") ? "video/mp4" : "video/webm"}
              />
              Your browser does not support HTML5 video.
            </video>
          </div>

          <div className="mt-2 text-sm">
            <p>
              Status:{" "}
              <span className="font-mono">
                {videoStatus[video.id] || "Not loaded"}
              </span>
            </p>
            <button
              className="mt-2 px-3 py-1 bg-blue-500 text-white rounded text-sm"
              onClick={() => {
                const videoElement = document.getElementById(
                  `video-${video.id}`
                ) as HTMLVideoElement;
                if (videoElement) {
                  videoElement.play().catch((e) => {
                    console.error(`Error playing video ${video.id}:`, e);
                    updateStatus(video.id, `Play error: ${e.message}`);
                  });
                }
              }}
            >
              Force play
            </button>
          </div>
        </div>
      ))}

      <div className="mt-8 p-4 bg-gray-100 rounded-lg">
        <h2 className="font-semibold mb-2">HTML5 Video Test</h2>
        <p className="text-sm mb-4">
          This is the most basic HTML5 video element:
        </p>

        <video controls width="100%" height="auto" playsInline>
          <source src="/videos/test.mp4" type="video/mp4" />
          Your browser does not support HTML5 video.
        </video>

        <div className="mt-4">
          <h3 className="font-semibold mb-2">Direct Embedded Video</h3>
          <iframe
            width="100%"
            height="200"
            src="https://www.youtube.com/embed/aqz-KE-bpKQ"
            title="YouTube video player"
            frameBorder="0"
            allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
            allowFullScreen
          ></iframe>
        </div>
      </div>
    </div>
  );
}
